@import url("https://fonts.googleapis.com/css?family=Baloo+Tammudu");
body {
  background: url("img/ForestBackground5-PixelArt.JPG") no-repeat top center fixed;
  background-size: cover;
  opacity: 0.9; }

.title * {
  font-family: 'Baloo Tammudu', cursive;
  font-size: 7em !important;
  margin-top: 30px;
  margin-bottom: -10px;
  color: #ddc;
  text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.9);
  background-clip: text; }

.controls {
  max-width: 960px; }

.panel-heading {
  padding: 8px !important; }

.panel-heading * {
  font-size: 2em !important; }

.panel-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100px;
  overflow: y-axis; }

@media screen and (max-width: 670px) {
  .pane-body {
    height: 10vh; } }

.panel-num {
  font-size: 3em;
  font-weight: bold; }

.btn {
  font-weight: bold;
  font-size: 1.2em; }

.btn-ctrls {
  min-width: 55px; }

.btn-dials {
  width: 40px; }

.board {
  padding: 1em; }

.row-box {
  min-width: 550px;
  height: 14px; }

.col-box {
  min-width: 550px;
  height: 14px; }

.box {
  margin-left: -1px;
  display: inline-block;
  border: 1px  solid burlywood;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 15px;
  width: 15px;
  border-radius: 20%; }

.box:hover {
  background-color: skyblue; }

.box:active {
  background-color: #000; }

.off {
  color: #fff;
  background: #7C511C; }

.on {
  color: #fff;
  background: #6BD425; }



